/* !important - הכי חזק במפל ההיררכיה */
.image-position1 {
  position: absolute;
  z-index: 2;
  right: 50%;
}

.image-position {
  position: relative;
  z-index: 2;
  right: 20%;
  margin: 2px;
}

h2 {
  color: rgb(6, 10, 8);
  padding: 25px;
  text-align: right;
  border-radius: 4px;
}

.container {
  z-index: 9;
  position: relative;
  background-color: rgba(148, 184, 241, 0.219);
  width: 60%;
  color: black;
  border: 5px double rgba(189, 153, 44, 0.421);
  /* box-shadow: #7fff00; */
  box-shadow: 20px 4px 8px rgba(0, 0, 0, 0.1);

  padding: 35px;
  border-radius: 2%;
  /* padding-left: 90px; */
  margin: 40px;
}

#MainContentRelative {
  display: relative;
}

@media (max-width: 600px) {
  .image-position {
    position: relative;
    right: 0%;
  }
  .image-position1 {
    /* position: relative; */
    right: 0%;
  }
}
